"use strict";

/* Rating score of customer */
const ratingCustomer = (() => {
    let setStatus = function () {
        $('.jsRatingCustomerScore').each(function () {
            if (parseFloat($(this).text()) > 0 && parseFloat($(this).text()) <= 10) {
                $(this).closest('.jsRatingCustomerItem')
                    .find('.jsRatingCustomerStatus')
                    .css('width', parseFloat($(this).text()) / 10 * 100 + '%')
            } else {
                console.log('Giá trị đầu vào của điểm đánh giá méo hợp lệ!');
            }
        })
    }
    setStatus();

    // Reset status bar when resize screen
    let resize;
    $(window).resize(function () {
        clearTimeout(resize);
        resize = setTimeout(function () {
            setStatus();
        }, 300);
    });

    // Draw circle chart by canvas
    let canvas = document.getElementById("jsRatingCustomerCircle") || false;

    if (canvas) {
        let ctx = canvas.getContext("2d");
        let eva = canvas.getAttribute('eva');
        let score = canvas.getAttribute('score');
        let des = canvas.getAttribute('des');

        if (parseFloat(score) >= 0 && score <= 10) {
            canvas.width = 200;
            canvas.height = 200;

            // Begin draw text 1
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.font = '400 16px Roboto';
            ctx.fillStyle = '#4A4A4A';
            ctx.fillText(eva, 100, 60);

            // Begin draw text 2
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.font = '700 36px Roboto';
            ctx.fillStyle = '#EBA32E';
            ctx.fillText(score, 100, 95);

            // Begin draw text 3
            ctx.textAlign = 'center';
            ctx.textBaseline = 'middle';
            ctx.font = '400 13px Roboto';
            ctx.fillStyle = '#4A4A4A';
            ctx.fillText(des, 100, 130);

            // Begin draw outer circle
            ctx.beginPath();
            ctx.arc(100, 100, 92, 0, 2 * Math.PI);
            ctx.strokeStyle = '#E2E2E2';
            ctx.lineWidth = 8;
            ctx.stroke();

            // Begin draw inner circle
            ctx.beginPath();
            ctx.arc(100, 100, 93, -0.5 * Math.PI, 2 * score / 10 * Math.PI - 0.5 * Math.PI);
            ctx.strokeStyle = '#EBA32E';
            ctx.lineWidth = 10;
            ctx.shadowBlur = 1;
            ctx.shadowOffsetX = 1;
            ctx.shadowOffsetY = 1;
            ctx.shadowColor = '#E2E2E2';
            ctx.stroke();
        } else {
            console.log('Giá trị đầu vào của điểm đánh giá méo hợp lệ!');
        }
    }
})();

export default ratingCustomer;


