"use strict";

/* noUiSlider - JavaScript Range Slider */
const uiSlider = (() => {
    let keypressSlider = document.getElementById('noUiSliderPickPrice') || false;
    let input0 = document.getElementById('noUiSliderPickPriceInput1') || false;
    let input1 = document.getElementById('noUiSliderPickPriceInput2') || false;
    let inputs = [input0, input1];

    if (keypressSlider) {
        noUiSlider.create(keypressSlider, {
            start: [4, 16],
            connect: true,
            // direction: 'rtl',
            range: {
                'min': 1,
                'max': 20
            },
            step: 0.5,
            // tooltips: true,
        });

        // Set value for input
        keypressSlider.noUiSlider.on('update', function (values, handle) {
            inputs[handle].setAttribute("value", values[handle]);
        });

        function setSliderHandle(i, value) {
            let r = [null, null];
            r[i] = value;
            keypressSlider.noUiSlider.set(r);
        }

        // Listen to keydown events on the input field.
        inputs.forEach(function (input, handle) {

            input.addEventListener('change', function () {
                setSliderHandle(handle, this.value);
            });

            input.addEventListener('keydown', function (e) {

                let values = keypressSlider.noUiSlider.get();
                let value = Number(values[handle]);
                // [[handle0_down, handle0_up], [handle1_down, handle1_up]]
                let steps = keypressSlider.noUiSlider.steps();
                // [down, up]
                let step = steps[handle];
                let position;
                // 13 is enter,
                // 38 is key up,
                // 40 is key down.
                switch (e.which) {
                    case 13:
                        setSliderHandle(handle, this.value);
                        break;
                    case 38:
                        // Get step to go increase slider value (up)
                        position = step[1];

                        // false = no step is set
                        if (position === false) {
                            position = 1;
                        }
                        // null = edge of slider
                        if (position !== null) {
                            setSliderHandle(handle, value + position);
                        }
                        break;
                    case 40:
                        position = step[0];
                        if (position === false) {
                            position = 1;
                        }
                        if (position !== null) {
                            setSliderHandle(handle, value - position);
                        }
                        break;
                }
            });
        });
    }
})();

export default uiSlider;


