"use strict";

/* Slide config */
const owlCarousel = (() => {
    // Slide highlights hotel & tour
    $(".js3ItemOwlCarousel").owlCarousel({
        loop: true,
        autoplay: true,
        margin: 30,
        responsive: {
            0: {
                items: 1
            },
            768: {
                items: 2
            },
            992: {
                items: 3
            }
        },
        nav: true,
        navText: [' <img class="" src="images/ic-sl.png" alt="">','<img class="" src="images/ic-sr.png" alt="">']
    })

    $(".js4ItemOwlCarousel").owlCarousel({
        loop: true,
        autoplay: true,
        margin: 20,
        responsive: {
            0: {
                items: 1
            },
            576: {
                items: 2
            },
            992: {
                items: 4
            }
        },
        nav: true,
        navText: [' <img class="" src="images/ic-sl.png" alt="">','<img class="" src="images/ic-sr.png" alt="">']
    })
})();

export default owlCarousel;


