"use strict";

/* Dropdown */
const dropdown = (() => {
    // Semantic UI
    $('.ui.dropdown')
        .dropdown({
            duration: 0
        });
    // Bootstap Datepicker
    $('.jsDatepicker').datepicker({
        format: "dd/mm/yyyy"
    });
})();

export default dropdown;


