"use strict";

/* Semantic UI autoSearch */
const autoSearch = (() => {
    let content = [{
        title: 'Bà Nà - Địa điểm Đà Nẵng',
        description: 'ba na - dai diem da nang',
        // url: 'https://semantic-ui.com/modules/search.html#/definition'
    }, {
        title: 'Phố Cổ Hội An - Địa điểm Quảng Nam',
        description: 'pho co hoi an - dia diem quang nam',
        // url: 'https://semantic-ui.com/modules/search.html#/definition'
    }];

    // Search tour
    $('#searchTour')
        .search({
            source: content,
            templates: {
                message: function () {
                    const html = '<div class="message empty"><div class="description">Không tìm thấy kết quả</div></div>';
                    return html;
                }
            },
        });

    // Search hotel
    $('#searchHotel')
        .search({
            source: content,
            templates: {
                message: function () {
                    const html = '<div class="message empty"><div class="description">Không tìm thấy kết quả</div></div>';
                    return html;
                }
            },
        });

    // Search car from
    $('#searchCarFrom')
        .search({
            source: content,
            templates: {
                message: function () {
                    const html = '<div class="message empty"><div class="description">Không tìm thấy kết quả</div></div>';
                    return html;
                }
            },
        });

    // Search car to
    $('#searchCarTo')
        .search({
            source: content,
            templates: {
                message: function () {
                    const html = '<div class="message empty"><div class="description">Không tìm thấy kết quả</div></div>';
                    return html;
                }
            },
        });
})();

export default autoSearch;


